.App {
  font-family: sans-serif;
  /* text-align: center; */
  --w: 50px;
  --margin: 2px;
}
.hexie {
  clip-path: polygon(50% 0, 100% 25%, 100% 75%, 50% 100%, 0 75%, 0 25%);
  width: var(--w);
  height: calc(var(--w) * 1.1547);
  margin: var(--margin);
  margin-bottom: calc(var(--margin) - var(--w) * 0.3886);
}

.colors {
  display: flex;
  align-items: center;
}

.picker {
  position: relative;
  display: inline-block;
}

.swatch {
  width: 28px;
  height: 28px;
  border-radius: 8px;
  border: 3px solid #fff;
  box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.1), inset 0 0 0 1px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  margin: 3px;
}

.popover {
  position: absolute;
  top: calc(100% + 2px);
  left: 0;
  border-radius: 9px;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
  z-index: 99;
}

.row {
  white-space: nowrap;
}

.row:nth-child(even) {
  margin-left: calc(var(--w) / 2 + 2px);
}
